
import React, { useState } from 'react';
import './style.css';
import register from './assets/register.png';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Contact = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = (event) => {
    event.preventDefault();

    if (validateEmail(email) && validatePassword(password)) {
      toast.success('Login successful!', { position: 'top-center' });

      setEmail('');
      setPassword('');
    } else {
      toast.error('Please enter valid email and password.', { position: 'top-center' });
    }
  };

  const validateEmail = (email) => {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return re.test(email);
  };

  const validatePassword = (password) => {
    return password.length >= 6;
  };

  return (
    <>
      <ToastContainer autoClose={500} />

      <center>
        <h3 className="title-sign-in">
         SignIn Your Account
        </h3>
      </center>
      <div className="outline-container">
        <div className="left" style={{ backgroundImage: `url(${register})`, backgroundSize: 'cover' }}></div>
        <div className="right">
          <div className="formbox">
            <form onSubmit={handleSubmit}>
              <p>E-mail</p>
              <br />
              <input
                type="email"
                value={email}
                placeholder="Enter your email"
                onChange={(e) => setEmail(e.target.value)}
              />
              <br /><br />
              <p>Password</p>
              <br />
              <input
                type="password"
                value={password}
                placeholder="Enter your password"
                onChange={(e) => setPassword(e.target.value)}
              />
              <br /><br /><br />
              <button type="submit">Sign In</button>
              <br /><br /><br />
              <p id="fpassword">Forgot password?</p>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Contact;
