import { Outlet, Link } from 'react-router-dom';
import { Navbar, Nav, Container, Form, FormControl } from 'react-bootstrap';

const Layout = () => {
  return (
    <>
      <Navbar bg="light" expand="lg" fixed="top">
        <Container fluid>
          <Navbar.Brand href="#">Furniture <span>Shop</span></Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarNav" />
          <Navbar.Collapse id="navbarNav">
            <Nav className="me-auto mb-2 mb-lg-0">
              <Nav.Link as={Link} to="/" className="nav-link active" aria-current="page">Home</Nav.Link>
              <Nav.Link as={Link} to="/About" className="nav-link">About</Nav.Link>
              <Nav.Link as={Link} to="/Product" className="nav-link">Product</Nav.Link>
              <Nav.Link as={Link} to="/Login" className="nav-link">Account</Nav.Link>
              <Nav.Link as={Link} to="/Cart" className="nav-link">Cart</Nav.Link>
            </Nav>
            <Form className="d-flex" role="search">
              <FormControl type="search" placeholder="Search" aria-label="Search" />
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <div className="mt-5 pt-3">
        <Outlet />
      </div>
    </>
  );
};

export default Layout;